import Logger from '../common/Logger';
import Util from '../common/Util';

/**
 * ajax,optionsajax
 * url:url,
 * header:,json,null
 * responseType:,,''
 * timeout:,0
 * requestParameter:,url
 * parameter: ,onSuccessonFail,null
 * debug:true,,false
 * onSuccess:,,null
 * onFail:,null
 * onAbort:,null
 * onError:,null
 * onTimeout:,null
 * @param {Object} options - nullundefined
 * @returns {XMLHttpRequest} xhr - XMLHttpRequest
 */
function get(options) {
    if (!options) {
        throw new Error('options!');
    }
    let url = options.url;
    if (!url) {
        throw new Error('url!');
    }

    const logger = Logger.getInstance();
    const headers = options.headers || null;
    const responseType = options.responseType || '';
    const timeout = options.timeout || 0;
    const requestParameter = options.requestParameter || null;
    if (requestParameter) {
        let queryString;
        if (Util.isString(requestParameter)) {
            queryString = requestParameter;
        } else if (Util.isObject(requestParameter)) {
            const paramArr = [];
            Util.forOwn(requestParameter, (value, key) => {
                if (Util.isArray(value) || Util.isObject(value)) {
                    paramArr.push(`${key}=${encodeURIComponent(JSON.stringify(value))}`);
                } else {
                    paramArr.push(`${key}=${encodeURIComponent(value)}`);
                }
            });
            queryString = paramArr.join('&');
        } else {
            throw new Error(`${JSON.stringify(requestParameter)}`);
        }
        
        const joinStr = url.indexOf('?') >= 0 ? '&' : '?';
        url = `${url}${joinStr}${queryString}`;
    }

    const parameter = options.parameter || null;
    const debug = options.debug || false;

    const onSuccess = options.onSuccess || null;
    const onFail = options.onFail || null;
    const onAbort = options.onAbort || null;
    const onError = options.onError || null;
    const onTimeout = options.onTimeout || null;

    const xhr = new XMLHttpRequest();
    if (!xhr) {
        throw new Error('XMLHttpRequest!');
    }

    let startTime = null;
    let endTime = null;

    xhr.onloadstart = event => {
        if (debug) {
            startTime = new Date().getTime();
            logger.log(`:${url}    :${startTime}`);
            logger.log(`:${JSON.stringify(parameter)}`);
            logger.log(event);
        }
    };
    xhr.onabort = event => {
        endTime = new Date().getTime();
        const diff = endTime - startTime;
        const errmsg = `:${url}    :${startTime}    :${diff}`;

        if (debug) {
            logger.log(errmsg);
            logger.log(`:${JSON.stringify(parameter)}`);
            logger.log(event);
        }

        if (onAbort) {
            onAbort(errmsg, parameter);
        }
    };
    xhr.onerror = event => {
        endTime = new Date().getTime();
        const diff = endTime - startTime;
        const errmsg = `:${url}    :${startTime}    :${diff}`;

        if (debug) {
            logger.log(errmsg);
            logger.log(`:${JSON.stringify(parameter)}`);
            logger.log(event);
        }

        if (onError) {
            onError(errmsg, parameter);
        }
    };
    xhr.ontimeout = event => {
        endTime = new Date().getTime();
        const diff = endTime - startTime;
        const errmsg = `:${url}    :${startTime}    :${diff}`;

        if (debug) {
            logger.log(errmsg);
            logger.log(`:${JSON.stringify(parameter)}`);
            logger.log(event);
        }

        if (onTimeout) {
            onTimeout(errmsg, parameter);
        }
    };
    xhr.onload = event => {
        endTime = new Date().getTime();
        const diff = endTime - startTime;

        // 2xx304
        if ((xhr.status >= 200 && xhr.status < 300) || xhr.status === 304) {
            if (debug) {
                logger.log(`:${url}    :${startTime}    :${diff}`);
                logger.log(`:${JSON.stringify(parameter)}`);
                logger.log(xhr.response);
            }
            if (onSuccess) {
                onSuccess(xhr.response, parameter);
            }
        } else {
            const errmsg = `:${url}    :${startTime}    :${diff}    :${xhr.status}`;
            if (debug) {
                logger.log(errmsg);
                logger.log(`:${JSON.stringify(parameter)}`);
                logger.log(event);
            }
            if (onFail) {
                onFail(errmsg, parameter);
            }
        }
    };
    xhr.open('GET', url);
    if (headers) {
        const keys = Util.keys(headers);
        for (let i = 0; i < keys.length; ++i) {
            const key = keys[i];
            const value = headers[key];
            xhr.setRequestHeader(key, value);
        }
    }
    xhr.responseType = responseType;
    xhr.timeout = timeout;
    xhr.send();
    return xhr;
}

