import Singleton from './Singleton';

/**
 * 
 */
export default class Factory extends Singleton {
    /**
     * 
     * @param  {object}     options 
     * @return {undefined}
     */
    constructor(options) {
        super(options);

        /**
         * 
         * @type {Object}
         */
        this._kvMap = {};
    }

    /**
     * 
     * @param {object} obj 
     */
    addTo(obj) {
        obj[this.constructor.name] = this;
    }

    /**
     * 
     * @return {object} 
     */
    getConfig() {
        return this._kvMap;
    }

    /**
     * key
     * @param {String|Number} keyString 
     */
    createKey(...keyString) {
        return keyString.filter(it => !!it).join('-');
    }

    /**
     * 
     * @param  {string} key   
     * @param  {all}    value 
     * @return {undefined}
     */
    register(key, value) {
        if (!key || !value) {
            return;
        }
        if (this._kvMap.hasOwnProperty(key)) {
            throw new Error(`${key}${this._kvMap[key]}, `);
        }

        this._kvMap[key] = value;
    }

    /**
     * 
     * @param {string} key 
     * @return {undefined}
     */
    unregister(key) {
        if (!key) {
            return;
        }
        if (!this._kvMap.hasOwnProperty(key)) {
            return;
        }

        delete this._kvMap[key];
    }

    /**
     * 
     * @param {string} key 
     */
    get(key) {
        return this._kvMap[key];
    }

    /**
     * 
     * @param key         
     * @returns {boolean} 
     */
    contains(key) {
        return this._kvMap.hasOwnProperty(key);
    }

    /**
     * 
     * @returns {undefined}
     */
    clear() {
        this._kvMap = {};
    }

    /**
     * 
     * @returns {undefined}
     */
    destroy() {
        this.clear();
        super.destroy();
    }
}



