import Static from '../clazz/Static';

/**
 * 
 * 
 */
export default class EventStore extends Static {
    static _kvMap = {};

    /**
     * 
     * @param  {string} key   key
     * @param  {string} value value
     * @return {undefined}
     */
    static register(key, value) {
        if (this._kvMap.hasOwnProperty(key)) {
            // throw new Error(`${key}`);
            console.warn(`${key}`);
        }

        this._kvMap[key] = value;

        Object.defineProperty(this, key, {
            get: () => this._kvMap[key],
            enumerable: true,
            configurable: true
        });
    }

    /**
     * 
     * @param  {object} kvMap key/value
     * @return {undefined}
     */
    static registerAll(kvMap) {
        for (const key in kvMap) {
            if (kvMap.hasOwnProperty(key)) {
                this.register(key, kvMap[key]);
            }
        }
    }

    /**
     * 
     * @param  {string} value 
     * @return {boolean} 
     */
    static contains(value) {
        for (const key in this) {
            if (this._kvMap.hasOwnProperty(key)) {
                if (this[key] === value) {
                    return true;
                }
            }
        }

        return false;
    }

    /**
     * 
     * @return {undefined}
     */
    static clear() {
        for (const key in this._kvMap) {
            if (this._kvMap.hasOwnProperty(key)) {
                delete this[key];
            }
        }

        this._kvMap = {};
    }
}



