/**
 * ,,,.
 */
export default class Vector {
    /**
     * Vector,x, yundefined,0.
     * @param {Number}[x=0]
     * @param {Number}[y=0]
     */
    constructor(x, y) {
        if (x === undefined) {
            this.x = 0;
        } else {
            this.x = x;
        }

        if (y === undefined) {
            this.y = 0;
        } else {
            this.y = y;
        }
    }

    /**
     * ,.
     * @param v
     * @return {object} Vector
     */
    minus(v) {
        const x = this.x - v.x;
        const y = this.y - v.y;
        return new Vector(x, y);
    }

    /**
     * 
     * @param v
     * @return {object} Vector
     */
    plus(v) {
        const x = this.x + v.x;
        const y = this.y + v.y;
        return new Vector(x, y);
    }

    /**
     * (xy)
     * @param {Number} n
     * @return {object} Vector
     */
    multiNumber(n) {
        const x = this.x * n;
        const y = this.y * n;
        return new Vector(x, y);
    }

    /**
     * 
     * @param {Number} n
     * @return {object} Vector
     */
    dividNumber(n) {
        const x = this.x / n;
        const y = this.y / n;
        return new Vector(x, y);
    }

    /**
     * 
     * 
     * Z
     * @param v
     * @return Number
     */
    cross(v) {
        return this.x * v.y - this.y * v.x;
    }

    /**
     * 
     * @param v
     * @return {Number} 
     */
    dot(v) {
        return this.x * v.x + this.y * v.y;
    }

    /**
     * 
     * @return {Number} 
     */
    length2() {
        return this.x * this.x + this.y * this.y;
    }

    /**
     * 
     * @return {Number} .
     */
    length() {
        return Math.sqrt(this.length2());
    }

    /**
     * .
     */
    normalize() {
        const length = this.length();
        this.x = this.x / length;
        this.y = this.y / length;
    }

    /**
     * 
     * 
     * 
     * @param {object} m
     * @return {object} newVec  Vector
     */
    crossMatrix(m) {
        const tmpVec = [this.x, this.y, 1];
        const newVec = new Vector(0, 0);
        newVec.x = tmpVec[0] * m.data[0][0] + tmpVec[1] * m.data[1][0] + tmpVec[2] * m.data[2][0];
        newVec.y = tmpVec[0] * m.data[0][1] + tmpVec[1] * m.data[1][1] + tmpVec[2] * m.data[2][1];

        return newVec;
    }

    /**
     * 
     * @param v
     * @return {Number} 
     */
    angleTo(v) {
        const cos = this.dot(v) / (this.length() * v.length());
        const arcA = Math.acos(cos);

        return arcA * 180 / Math.PI;
    }
}



